// src/components/GoogleMap.js
import { GoogleMap, InfoWindow, Marker, Polygon } from "@react-google-maps/api";
import React, { useRef, useState } from "react";
import HotelInfoWindow from "./MapInfoWindow";

const containerStyle = {
  width: "100%",
  height: "400px",
};

export const GoogleMapsComponent = ({ markers }) => {
  const [selectedMarker, setSelectedMarker] = useState(null);
  const [forceUpdate, setForceUpdate] = useState(false);
  const infoWindowRef = useRef(null);
  const [path, setPath] = useState({
    Texas: [
      { "lat": 36.5, "lng": -103 },
      { "lat": 36.5, "lng": -94 },
      { "lat": 32, "lng": -94 },
      { "lat": 32, "lng": -106 },
      { "lat": 36.5, "lng": -106 }
    ]
  });


  const onMarkerClick = (marker) => {
    console.log(marker);
    setSelectedMarker(marker);
  };

  return (
    <GoogleMap
      mapContainerStyle={containerStyle}
      center={{ lat: 39.828175, lng: -98.5795 }}
      zoom={4}
    >
      {markers.map((marker, index) => {
        const { id, location } = marker;
        return (
          <Marker
            onClick={() => onMarkerClick(marker)}
            key={id}
            position={{ lat: location.lat, lng: location.lng }}
            title={"marker.title"}
          />
        );
      })}

      {selectedMarker && (
        <InfoWindow
          position={{
            lat: selectedMarker.location.lat,
            lng: selectedMarker.location.lng,
          }}
          ref={infoWindowRef}
          onCloseClick={() => setSelectedMarker(null)}
        >
          {/* <HotelMapMarkerSingle {...selectedMarker}/> */}
          <div style={{ maxWidth: "200px", minHeight: "200px" }}>
            <HotelInfoWindow
              {...selectedMarker}
              onCloseClick={() => {
                setForceUpdate((prevState) => !prevState);
              }}
            />
          </div>
        </InfoWindow>
      )}

      <Polygon
        paths={path}
        editable
            draggable
       
      />
    </GoogleMap>
  );
};
